import { Dispatch, SetStateAction } from "react"

function createHandlePillBoxResizeObservation(
  breakpoint: number,
  setIsLargerThanBreakpoint: Dispatch<SetStateAction<boolean>>,
) {
  return function (entries: Array<ResizeObserverEntry>) {
    setIsLargerThanBreakpoint((entries[0].target.scrollHeight ?? 0) > breakpoint)
  }
}

export { createHandlePillBoxResizeObservation }
